"use client";
import { userContext } from "@/app/context/user";
import { JSX, ReactNode, useContext, useEffect, useState } from "react";
import { Card, CardContent } from "../ui/card";
import { ListChecksIcon, TicketCheckIcon } from "lucide-react";
import getWebcontainerState from "@/app/helpers/getWebContainerState";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "../ui/tabs";
import { Span } from "next/dist/trace";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible"
import { WebContainerType } from "@/app/types/user";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "../ui/accordion";
import { ScrollArea } from "../ui/scroll-area";
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';

function WrapperOfString({code} : {code : string}) {
  return (
  
    <SyntaxHighlighter language="tsx">
      {code}
    </SyntaxHighlighter>
  )
}


export default function WebContainerBlock({ id }: { id: string }) {
  const { webContainerState, setWebContainerState } = useContext(userContext)!;
  const [codePreview, setCodePreview] = useState<string>("");
  const [finalArray, setFinalArray] = useState<ReactNode>(<></>);


  function FilesShowing({ files ,parser}: { files: WebContainerType[] , parser: DOMParser}) {

    return (
      <div className="flex flex-col gap-3 w-full h-full">
        {files.map((item) =>

          item.file ?
            <span key={item.name} onClick={() => (setCodePreview(item.content!))} className="hover:cursor-pointer bg-amber-100 rounded-[11px] p-3">{item.name}</span>

            :
            <Accordion key={item.name} type="single" collapsible>
              <AccordionItem value={`${Math.random()}`} className="hover:cursor-pointer bg-amber-100 rounded-[11px] p-3">
                <AccordionTrigger>{item.name}</AccordionTrigger>
                <ScrollArea>
                <AccordionContent>
                  <FilesShowing files={item.children ?? []} parser={parser}/>
                </AccordionContent>

                </ScrollArea>

              </AccordionItem>
            </Accordion>
        )
        }
      </div>
    )
  }

  useEffect(() => {
    async function getwebstate() {
      const messages = await getWebcontainerState(id);
      console.log(messages)
      if (messages.data != null) {
        const stateInJson = JSON.parse(messages.data?.state?.files!);
        console.log("result ", stateInJson)
        setWebContainerState(stateInJson)
        const parser = new DOMParser()
        setFinalArray(<FilesShowing files={stateInJson} parser={parser}/>);
      }

    }

    getwebstate()
  }, []);


  return (
    <div className="w-full h-full p-3 ">
      <Tabs defaultValue="code" className="w-full h-full">
        <TabsList>
          <TabsTrigger value="code" className="hover:cursor-pointer">
            Code
          </TabsTrigger>
          <TabsTrigger value="preview" className="hover:cursor-pointer">
            Preview
          </TabsTrigger>
        </TabsList>

        <TabsContent value="code" className="flex gap-3 h-[97%]">
          {/* Slider */}
          <Card className="w-[30%] h-full">
            <ScrollArea className="h-[99%]">
            <CardContent className="w-full h-full">
              {finalArray ? finalArray : "No files found"}
            </CardContent>
            </ScrollArea>
          </Card>

          <Card className="w-[67%]">
              <ScrollArea className="h-[99%]">
    
            <CardContent>
              <WrapperOfString code={codePreview}/>
            </CardContent>
    </ScrollArea>
          </Card>
        </TabsContent>
        <TabsContent value="preview">

        </TabsContent>
      </Tabs>
    </div>
  );
}
